body {
  background: #FF5F6D;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FFC371, #FF5F6D);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#FFC371), to(#FF5F6D));
  background: -webkit-linear-gradient(left, #FFC371, #FF5F6D);
  background: -o-linear-gradient(left, #FFC371, #FF5F6D);
  background: linear-gradient(to right, #FFC371, #FF5F6D); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 5px;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #cb2d3e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #f76256, #cb2d3e);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#f76256), to(#cb2d3e));
  background: -webkit-linear-gradient(left, #f76256, #cb2d3e);
  background: -o-linear-gradient(left, #f76256, #cb2d3e);
  background: linear-gradient(to right, #f76256, #cb2d3e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  ;
  height: 200px;
  padding: 40px;
  color: white;
  border-radius: 100px;

}

.App-title {

  font-size: 4.5em;
}

.App-intro {
  font-size: large;
}

.form-group {
  margin-top: 100px;
}

.form-control {
  height: 45px;;
}

.btn {
  margin-top: 30px;
}

.urls {
  margin-top: 20px;
}

.long {
  text-align: left;
}

.short {
  text-align: right;
}

a {
  color: #96070c;
  font-size: 1.5em;
  text-decoration: none;
  text-align: right;
}

@media only screen and (max-width: 991px) {
  .long {
    text-align: center;
  }
  
  .short {
    text-align: center;
  }
} 

@media only screen and (max-width: 500px) {
  .App-title {
    font-size: 2.5em;
  }
  .App-header {
    padding: 55px;
  }
} 


